.wrapper-serial-log {
    width: 80%;
    flex: auto;
    padding: 8px 12px;
    overflow-y: auto;
    margin: 0 auto;

    &::-webkit-scrollbar {
        width: 6px;
        height: 6px;
    }

    pre {
        font-size: 13px;
        word-break: break-word;
        white-space: pre-wrap;
        word-wrap: break-word;
    }
}

.footer-btn {
    width: 80%;
    margin: 10px auto;
    text-align: right;
    min-height: 180px;
    height: 180px;

    button {
        font-size: 18px;
        padding: 3px 8px;
        border: none;
        outline: none;
        cursor: pointer;

        &.send {
            margin-right: 12px;
            background-color: orange !important;
            transition: all .25s ease;

            &:hover {
                background-color: #0066BC !important;
            }
        }
    }

    .selector-data-type {
        float: left;
        outline: none;
        font-size: 16px;
        padding: 3px 8px;
        border: none;
        background-color: #d9d9d9;
        color: #333;
    }

    .sendbox {
        float: left;
        outline: none;
        width: 100%;
        height: 120px;
        font-size: 16px;
        padding: 4px 8px;
        border: none;
        display: block;
        margin-bottom: 12px;
        resize: none;
    }
}