.search-box {
    height: 40px;
    text-align: center;
    box-shadow: 1px 1px 3px #A9AAAB;

    input {
        width: 90%;
        height: 30px;
        padding-left: 10px;
        margin: 5px 0;
        border: 1px solid transparent;
        outline: none;
        border-radius: 30px;
        transition: all .2s ease;

        &:focus {
            border: 1px solid skyblue;
        }
    }
}