.wrapper-item-firmware {
    flex: 0 0 33.3333333%;

    article {
        position: relative;
        display: flex;
        flex-direction: column;
        width: 90%;
        height: 280px;
        margin: 16px auto;
        border: 1px solid lightgray;
        box-shadow: 1px 1px 3px lightgray;
        transition: all .2s ease;

        &:hover {
            box-shadow: 3px 3px 3px lightgray;
        }

        h2 {
            font-size: 16px;
            border-bottom: 1px solid lightgray;
            padding: 4px 8px;

            .tag {
                font-size: 10px;
                margin: 0 8px;
                padding: 4px 8px;
                border-radius: 6px;
            }

            .selector-version {
                float: right;
                outline: none;
                margin-top: 2px;
                background-color: transparent;
            }
        }

        p {
            padding: 4px 8px;
            font-size: 13px;
            flex: 1;
        }

        .footer {
            text-align: right;
            padding: 0 12px;
            margin-bottom: 6px;
            height: 30px;
            line-height: 30px;

            .more {
                font-size: 12px;
                float: left;
                cursor: pointer;
                color: #0099CC;
            }

            button {
                position: relative;
                border: 1px solid #333;
                padding: 4px 10px;
                cursor: pointer;
                transition: all .25s ease;
                overflow: hidden;

                i, span {
                    position: relative;
                    z-index: 2;
                    transition: all .25s ease;
                }

                &.burn i {
                    color: #FF0033;
                }

                span {
                    margin-left: 6px;
                }

                .mask, .progress {
                    position: absolute;
                    top: 0;
                    left: 0;
                    margin: auto;
                    z-index: 1;
                }
 
                .progress {
                    width: 0;
                    height: 100%;
                    background-color: #00A950;
                }

                &.burn .mask, &.remove .mask {
                    width: 0;
                    height: 100%;
                    background-color: #FF0033;
                }

                &.remove {
                    margin-right: 12px;
                }

                &.remove .mask {
                    background-color: orange;
                }

                &.download .mask {
                    width: 100%;
                    height: 0;
                    background-color: rgb(42, 136, 230);
                }

                &.downloading .mask {
                    display: none;
                }

                &:hover {
                    border-color: transparent;

                    i, span {
                        color: #FFFFFF;
                    }

                    &.burn .mask, &.remove .mask {
                        animation: fill-horizontal .3s ease forwards;
                    }

                    &.download .mask {
                        animation: fill-vertical .3s ease forwards;
                    }
                }

                &.downloading:hover {
                    border-color: #333;

                    i, span {
                        color: #333;
                    }
                }
            }
        }
    }
}