.header-form {
    padding: 0 10px;
    .btn {
        padding: 0 10px;
        display: inline-block;
        float: right;
        height: 30px;
        line-height: 30px;
        margin: 10px 6px;
        cursor: pointer;

        i, span {
            font-size: 13px;
        }

        i {
            margin-right: 6px;
        }
    }

    input[type=text] {
        width: 180px;
        font-size: 18px;
        outline: none;
        margin-left: 4px;
        padding-left: 4px;
    }
}