@keyframes fill-horizontal {
    0% {
        width: 0;
    }
    100% {
        width: 100%;
    }
}

@keyframes fill-vertical {
    0% {
        height: 0;
    }
    100% {
        height: 100%;
    }
}

@keyframes slide-in {
    0% {
        transform: translateX(-100%);
    }
    100% {
        transform: translateX(0);
    }
}

@keyframes slide-out {
    0% {
        transform: translateX(0);
    }
    100% {
        transform: translateX(-100%);
    }
}

@keyframes ball-pulse-sync {
    33% {
        transform: translateY(10px);
    }
  
    66% {
        transform: translateY(-10px);
    }
  
    100% {
        transform: translateY(0);
    }
}