.list-device {
    width: 240px;
    height: 100%;
    display: flex;
    flex-direction: column;
}

.item-device {
    flex: 1;
    text-align: center;
    cursor: pointer;
    opacity: 0.8;
    transition: all .2s ease;
    position: relative;

    figure {
        position: absolute;
        top: 0;
        bottom: 0;
        left: 0;
        right: 0;
        margin: auto;
        height: 200px;
    }

    .avatar {
        width: 150px;
        filter: grayscale(1);
        transition: all .2s ease;
    }

    figcaption {
        margin-top: 12px;
        font-size: 18px;
    }

    &.selected {
        opacity: 1;
    }

    &.selected .avatar, &:hover .avatar{
        filter: grayscale(0);
    }
}