@import './theme';
@import './animation';

* {
    padding: 0;
    margin: 0;
    box-sizing: border-box;
    font-family: Verdana, 'luximb', 'Cascadia Code', 'Source Code Pro';
}

p, a, span, i, figcaption {
    font-family: Verdana, Geneva, Tahoma, sans-serif;
    -webkit-user-select: none;
}

button {
    border: none;
    background-color: transparent;
    outline: none;
}

ul, li {
    text-decoration: none;
    list-style: none;
}

html, body {
    width: 100vw;
    height: 100vh;
}

#app {
    width: 100%;
    height: 100%;
}

.main {
    width: 100%;
    height: 100%;
    transition: all .2s ease;
    display: flex;
    flex-direction: column;
}

.topbar-logo {
    width: 28px;
    margin: 6px 10px;
    display: block;
    float: left;
}