.container-firmware {
    flex: 1;
    overflow: hidden;
    display: flex;
    flex-direction: row;
}

.wrapper-firmware-list {
    position: relative;
    display: flex;
    flex: 1;
    flex-direction: column;

    >.list {
        z-index: 1;
        position: absolute;
        top: 40px;
        left: 0;
        width: 100%;
        height: calc(100% - 40px);
        overflow-y: auto;

        &::-webkit-scrollbar {
            width: 6px;
            height: 6px;
        }

        &.active {
            z-index: 2;
        }
    }
}