.full-main {
    $header-height: 40px;

    position: absolute;
    top: $header-height;
    left: 0;
    width: 100%;
    height: calc(100% - #{$header-height});
    transition: all .25s ease;
    opacity: 0;
    z-index: 3;

    &.fade-in {
        opacity: 1;
    }

    &.fade-out {
        opacity: 0;
    }

    .mask {
        position: absolute;
        width: 100%;
        height: 100%;
        top: 0;
        left: 0;
        background-color: rgba(0, 0, 0, 0.88);
    }

    .modal {
        position: relative;
        width: 100%;
        height: 100%;
        z-index: 2;
        padding-top: 60px;
        display: flex;
        flex-direction: column;
    }
}