.light {
    $main-color: rgb(255, 255, 255);
    $top-bar-color: rgb(42, 136, 230);
    $top-bar-focus-color: rgb(29, 124, 219);
    $header-color: rgb(226, 226, 226);
    $device-list-bgcolor: #E5E6E7;
    $device-item-bgcolor: #D1D2D3;
    $firmware-list-bgcolor: #EDEEEF;
    $firmware-scrollbar-forecolor: #C5C6C7;
    $firmware-scrollbar-bgcolor: transparent;
    $search-box-bgcolor: #EDEEEF;
    $btn-erase: #00A950;
    $btn-open-com: rgb(42, 136, 230);
    $text-color-black: #333;
    $warning-red: #FF0033;

    &.main { background-color: $main-color }

    .app-top-bar { background-color: $top-bar-color }
    .app-top-bar .logo span { color: $main-color }
    .app-top-bar .control-box .btn-control:hover { background-color: $top-bar-focus-color }
    .app-top-bar .control-box .btn-control i { color: $main-color }
    .app-header { background-color: $header-color }

    .app-header .header-form {
        .btn.erase { background-color: $btn-erase }
        .btn.open { background-color: $btn-open-com }
        .btn i, .btn span { color: $main-color }
    }
    
    .container-firmware .list-device { background-color: $device-list-bgcolor }
    .container-firmware .item-device.selected, .container-firmware .item-device:hover { background-color: $device-item-bgcolor }

    .container-firmware .search-box { background-color: $search-box-bgcolor }

    .container-firmware .wrapper-firmware-list {
        background-color: $firmware-list-bgcolor;
        .list {
            background-color: $firmware-list-bgcolor;
        }
        .list::-webkit-scrollbar-track { background-color: $firmware-scrollbar-bgcolor }
        .list::-webkit-scrollbar-thumb { background-color: $firmware-scrollbar-forecolor }
        .tag {
            background-color: $btn-erase;
            color: $main-color;
        }
    }

    .modal .wrapper-serial-log {
        background-color: $firmware-list-bgcolor;
        color: $text-color-black;
        &::-webkit-scrollbar-track { background-color: $firmware-scrollbar-bgcolor }
        &::-webkit-scrollbar-thumb { background-color: $firmware-scrollbar-forecolor }
    }

    .modal .footer-btn button {
        background-color: $warning-red;
        color: $main-color;
    }

    .modal .panel-preference {
        background-color: $firmware-list-bgcolor;
        h2, h3, label, select, option { color: $text-color-black }
    }
}
