.app-top-bar {
    $header-height: 40px;
    $header-font-size: 20px;

    width: 100%;
    height: $header-height;
    padding-left: 6px;
    -webkit-app-region: drag;

    .logo {
        width: 200px;
        height: 100%;
        float: left;
        line-height: $header-height;
        span {
            font-size: $header-font-size
        }
    }

    .control-box {
        height: 100%;
        float: right;
        text-align: right;
        line-height: $header-height;
        -webkit-app-region: no-drag;
        .btn-control {
            height: 100%;
            cursor: pointer;
            transition: all .15s ease;
            i {
                padding: 0 18px;
                font-size: $header-font-size
            }
        }
    }
}