.wrapper-panel-preference {
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
}

.panel-preference {
    position: absolute;
    top: 0;
    left: 0;
    width: 300px;
    height: 100%;
    padding: 10px 0 0 8px;
    transition: all .2s ease;

    &.slide-in {
        animation: slide-in .25s ease forwards;
    }

    &.slide-out {
        animation: slide-out .25s ease forwards;
    }

    h2 {
        font-size: 32px;
        margin-bottom: 20px;
    }

    h3 {
        font-size: 24px;
        margin-bottom: 16px;
    }

    .form-preference {
        .form-item {
            margin: 10px 6px;
            label {
                font-size: 16px;
                display: inline-block;
                min-width: 140px;
            }
        }
    }

    select {
        outline: none;
        border: none;
        font-size: 16px;
        width: 120px;
    }

    option {
        font-size: 16px;
    }

    footer {
        position: absolute;
        height: 32px;
        bottom: 10px;
        right: 20px;

        button.btn {
            font-size: 20px;
            margin-left: 10px;
            float: right;
            padding: 4px 16px;
            cursor: pointer;
        }
    }

    .btn-update {
        font-size: 14px;
        cursor: pointer;
        text-decoration: underline;
        color: royalblue;
    }
}