$form-height: 50px;

.header-form-item {
    display: inline-block;
    height: $form-height;
    line-height: $form-height;

    i, span {
        margin-left: 10px;
        font-size: 18px;
    }

    select {
        margin-left: 10px;
        padding: 3px 10px;
        font-size: 18px;
        width: 200px;
        height: 30px;
        outline: none;
        padding-left: 6px;
        border: none;
        background-color: #cecece;

        option {
            background-color: #d9d9d9;
            border: none;
        }
    }
}